.menu {
  background-color: $colorRed;

  &__content {
    display: block;
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;
  }

  &__top {
    margin-bottom: 40px;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
    text-decoration: none;
    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: bold;
    font-size: 16px;
    line-height: 27px;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
      
    &--phone-1 {
      margin-bottom: 8px;
    
    }

    &--phone-2 {
      margin-bottom: 16px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      padding-bottom: 8px;
      border-bottom: 1px solid white;
    }
  }
}
